.image {
    width: 100vw;
    max-width: 600px;
}

.section {
    max-width: 600px;
    margin: 20px;
}

.container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

@media (max-width: 1200px) {
    .container {
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 600px) {

    .image,
    .section {
        width: 90vw;
    }

}

.name {
    color: white;
    font-weight: 600;
    font-size: 25px;
}

.description {
    font-size: 20px;
    color: white;
}

.price {
    font-size: 20px;
    color: rgb(255, 89, 78);
}