.container {
    background-color: rgb(51, 51, 51);
    position: sticky;
    left: 0;
    top: 0;
    z-index: 2;
    display: block;
    height: 100px;
}



.link {
    color: white;
    text-decoration: none;
    font-size: 20px;
    padding: 10px;
    border-radius: 20px;
    background-color: rgb(51, 51, 51);
    transition: 500ms;
    margin: 10px;
    width: max-content;
}

.link:hover {
    background-color: rgb(80, 80, 80);
    color: rgb(255, 89, 78);
    padding-left: 20px;
    padding-right: 20px;
}

.menu {
    float: right;
    margin-right: 20px;
    width: min-content;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    margin-left: 0;
    padding-left: 0;
}

@media (max-width: 822px) {
    .menu {
        float: none;
    }

    .container {
        height: fit-content;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.title {
    font-size: 40px;
    color: rgb(255, 89, 78);
    float: left;
    margin: 20px;
    margin-right: 0;
    font-weight: 500;
}

.logo {
    color: rgb(255, 89, 78);
    font-size: 100px;
    font-weight: 500;
}

.logoHolder{
    background-color: rgb(51, 51, 51);
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.logoImg{
    margin: 15px;
    height: 110px;
    border-radius: 20px;
}