.header{
    margin-top: 20px;
    margin-bottom: 20px;
    color: white;
    font-size: medium;
    text-align: center;
    font-weight: normal;
}
.text{
    margin-top: 20px;
    margin-bottom: 20px;
    color: white;
    font-size: medium;
    width: max-content;
    margin: 0;
    margin-left: 20px;
}
.image{
    height: 80vh;
    width: 80vh;
    border-radius: 20px;
    margin-right: 20px;
}
.holder{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: fit-content;
    margin: auto;
    margin-bottom: 20px;
    align-items: flex-start;
}