.productContainer {
    display: inline-block;
    margin: 20px;
    border-radius: 20px;
    width: 300px;
    background-color: rgb(44, 44, 44);
    cursor: pointer;
    transition: 200ms;
    text-decoration: none;
    height: 360px;
}

.flexHolder {
    height: 420px;
    margin-top: 10px;
}


.container {
    max-width: 1020px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.image {
    width: 300px;
    height: 300px;
    border-radius: 20px 20px 0 0;
    margin-bottom: 5px;
    opacity: 0.5;
}

.name {
    transition: 200ms;
    font-size: 24px;
    color: white;
    margin-left: 20px;
}

.price {
    font-size: 18px;
    color: rgb(255, 89, 78);
    text-align: right;
    margin-right: 20px;
    margin-bottom: 20px;
    transition: 200ms;
}

@media(min-width: 600px) {
    .productContainer:hover>.price {
        font-size: 20px;
    }

    .productContainer:hover>.name {
        font-size: 26px;
    }

    .productContainer:hover {
        background-color: rgb(58, 58, 58);
        margin-bottom: 0;
        margin-top: 18px;
        z-index: 1;
    }
}

@media(max-width: 600px) {
    .image {
        border-radius: 0;
    }

    .flexHolder {
        border-bottom: solid white 1px;
        width: 100vw;
        display: flex;
        justify-content: center;
    }

    .productContainer {
        background-color: rgb(24, 24, 24);
    }
}

